.App {
  text-align: left;
  color: white;
  font-family: Fira Code;

  padding-left: 50px;
  padding-top: 30px;
}

@media (max-width: 768px) {
  .App {
    padding: 30px 30px;
  }
}

.mainTitle {
  /* BREAK FREE FROM THE SURVEILLANCE STATE */

  font-style: normal;
  font-weight: normal;

  color: #ffffff;
}

form > label {
  color: #fff;
}

.submitButton {
  background-color: #f2f2f2;
  text-decoration: none;
  color: #000;
  font-weight: 500;
  padding: 10px 30px;
  border-radius: 2.5px;

  margin-top: 5px;
}

.demoimg {
  padding-top: 20px;
  max-width: 100%;
}

.demoimg > img {
  height: auto;
  max-width: 100%;
}

.demonstrationTable {
  margin: auto;
  width: 80%;
}

.demonstration {
  color: white;
  text-align: center;
}

.inputContainer {
  font-family: Fira Code;
  color: white;
}

.mainMobile {
  font-size: 40px;
  margin-bottom: 20px;
}

.mainDesktop {
  font-size: 64px;
  line-height: 84px;
}

.gif {
  width: 100%;
  height: 100%;
}

.plussign {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 200px;
  font-weight: lighter;
}

.break {
  background-color: white;
  width: 80%;
  margin: auto;
  margin-bottom: 30px;
}

.input {
  width: 363px;
  height: 363px;
  border: 1px solid white;
  margin: auto;
  position: relative;
}

.link {
  color: white;
  text-decoration: underline;
  text-underline-offset: 5px;
}

.explanation {
  font-size: 20px;
  margin-top: 10px;
  max-width: 1000px;
}

.overlay {
  background-color: black;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  z-index: 10;
  opacity: 0.8;
}

@keyframes fadeIn {
  from {
    margin-top: 10px;
    opacity: 0;
  }
  to {
    margin-top: 0px;
    opacity: 1;
  }
}

.center {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-family: Fira Code;
  text-align: center;
  animation-name: fadeIn;
  animation-duration: 2s;
}

.exitButton {
  position: absolute;
  top: 7px;
  left: 20px;
  font-size: 20px;
  cursor: pointer;
}

.buttonProcessing {
  padding-left: 0px !important;
}

.imageContainer {
  margin-top: 20px;
  width: 100%;
  text-align: center;
  /* background-color: red; */
}

.processingImg {
  max-width: 80%;
  max-height: 600px;

  margin: 0 auto;

  border-radius: 15px;
}

.imageConfirm__buttonContainer {
  margin-top: 25px;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.imageConfirm__button {
  padding: 10px 20px;
  margin: 0 10px;
}

.imageConfirm__button--cancel {
  color: white;
  background-color: red;
  border: none;
  outline: none;
}

.imageConfirm__button--cloak {
  color: white;
  background-color: green;
  border: none;
  outline: none;
}

.processButton {
  position: relative;
  top: -20px;
  font-size: 15px;
}

.buttonProcessing {
  width: 80%;
  /* background-color: red; */
  margin: auto !important;
}

.colbutton {
  background-color: red;
}

.processingTitle {
  padding-top: 40px;
  padding-bottom: 20px;
  padding-left: 20px;
}

