.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  color: white;
  font-family: Fira Code;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation-duration: 2s;
  animation-name: fadeIn;
  text-align: center;
}

body {
  padding: 0;
}

.processButton {
  padding-left: 0px !important;
}

.gradient1 {
  position: absolute;
  width: 422px;
  height: 422px;
  left: 0px;
  top: 0px;
  border-radius: 50%;
  /* background: radial-gradient(56.37% 56.37% at 17.04% 21.18%, rgba(150, 114, 255, 0.6) 0%, rgba(0, 178, 255, 0.428125) 28.65%, rgba(223, 255, 23, 0.259375) 56.77%, rgba(244, 214, 49, 0.209375) 67.71%, rgba(255, 107, 0, 0.153125) 84.37%, rgba(255, 255, 255, 0) 100%); */
  /* background: radial-gradient(
    circle,
    rgba(150, 114, 255, 0.6) 0%,
    rgba(0, 178, 255, 0.428125) 20%,
    rgba(223, 255, 23, 0.259375) 40%,
    rgba(244, 214, 49, 0.209375) 59%,
    rgba(255, 107, 0, 0.153125) 70%,
    rgba(0, 0, 0, 0) 100%
  ); */
  animation: move 30s ease-in-out infinite;
}
@keyframes move {
  0% {
    transform: scale(1) translate(10px, -30px);
  }
  38% {
    transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg);
  }
  40% {
    transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg);
  }
  78% {
    transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg);
  }
  80% {
    transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg);
  }
  100% {
    transform: scale(1) translate(10px, -30px);
  }
}

.gradient2 {
  position: fixed;
  width: 259px;
  height: 291px;
  right: -25px;
  bottom: -50px;
  background: radial-gradient(
    56.37% 56.37% at 17.04% 21.18%,
    rgba(150, 114, 255, 0.6) 0%,
    rgba(0, 178, 255, 0.428125) 28.65%,
    rgba(223, 255, 23, 0.259375) 56.77%,
    rgba(244, 214, 49, 0.209375) 67.71%,
    rgba(255, 107, 0, 0.153125) 84.38%,
    rgba(255, 255, 255, 0) 100%
  );
  transform: rotate(-165.09deg);
}

.heart {
  background: radial-gradient(
    39.87% 40.55% at 45.64% 43.8%,
    rgba(66, 0, 255, 0.3) 0%,
    rgba(0, 178, 255, 0.2) 20.48%,
    rgba(219, 255, 0, 0.2) 30.08%,
    rgba(255, 215, 0, 0.2) 61.46%,
    rgba(255, 107, 0, 0.15) 75.52%,
    rgba(255, 0, 0, 0.05) 88.54%,
    rgba(0, 0, 0, 0) 100%
  );
  position: absolute;
  top: 0;
  left: -50%;
  width: 1705px;
  height: 728px;
  min-width: 360px;
  border-radius: 50%;
  pointer-events: none;
  transform: rotate3d(1, 1, 1, 45deg);
  animation: zoom 40s ease-in-out infinite;
}

@keyframes zoom {
  0% {
    transform: scale(2) translate(20%, 100vh) rotate3d(1, 1, 1, 45deg);

    opacity: 0.6;
  }
  /* 38% {
    transform: scale(1.9) translate3d(0px, 20px, 0px) rotate3d(1, 1, 1, 85deg);
    opacity: 0.9;
  } */
  40% {
    transform: scale(2) translate(60%, 20%) rotate3d(1, 1, 1, 85deg);
    opacity: 0.6;
  }
  /* 78% {
    transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg);
    opacity: 0.8;
  }*/
  80% {
    transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg);
  }
  100% {
    transform: scale(2) translate(20%, 100vh) rotate3d(1, 1, 1, 45deg);

    opacity: 0.6;
  }
}

.blob {
  /* background: radial-gradient(
    39.87% 40.55% at 45.64% 43.8%,
    rgba(66, 0, 255, 0.3) 0%,
    rgba(0, 178, 255, 0.2) 20.48%,
    rgba(219, 255, 0, 0.2) 30.08%,
    rgba(255, 215, 0, 0.2) 61.46%,
    rgba(255, 107, 0, 0.15) 75.52%,
    rgba(255, 0, 0, 0.05) 88.54%,
    rgba(0, 0, 0, 0) 100%
  ); */

  background: radial-gradient(
    39.87% 40.55% at 45.64% 43.8%,
    rgba(150, 114, 255, 0.4) 0%,
    rgba(0, 178, 255, 0.328125) 20%,
    rgba(223, 255, 23, 0.259375) 40%,
    rgba(244, 214, 49, 0.209375) 59%,
    rgba(255, 107, 0, 0.153125) 70%,
    rgba(0, 0, 0, 0) 100%
  );

  /* background: #fff; */
  position: absolute;
  top: 0;
  left: 0%;
  width: 605px;
  height: 808px;
  min-width: 360px;
  border-radius: 50%;
  pointer-events: none;

  animation: zip 60s ease-in-out infinite;
}

@keyframes zip {
  0% {
    transform: translate(-5%, -100vh) rotate(45deg);
    width: 605px;
    height: 1208px;
    opacity: 0.5;
  }
  /* 38% {
    transform: scale(1.9) translate3d(0px, 20px, 0px) rotate3d(1, 1, 1, 85deg);
    opacity: 0.9;
  } */
  18% {
    transform: translate(-90%, 110%) rotate(365deg);
    width: 705px;
    height: 308px;
    opacity: 0.5;
  }
  24% {
    opacity: 0.3;
  }

  40% {
    transform: translate(90%, -50%) rotate(-185deg);
    width: 805px;
    height: 508px;
  }
  /* 78% {
    transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg);
    opacity: 0.8;
  }*/
  70% {
    transform: translate(60%, 120%) rotate(-85deg);
    width: 605px;
    height: 908px;
    opacity: 0.4;
  }
  80% {
    transform: translate(80vw, -50vh) rotate(-20deg);
    width: 605px;
    height: 1008px;
    opacity: 0.8;
  }
  100% {
    transform: translate(-50%, -80vh) rotate3d(1, 1, 1, 45deg);
    width: 305px;
    height: 1208px;
    opacity: 0.8;
  }
}

