.root {
  height: 100vh;
  width: 100vw;
  /* background: #000000; */
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.grid {
  /* height: 100%; */
  width: 80%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.logo {
  padding-top: 120px;
  /* height: 120px; */
}
.logoImg {
  width: 72px;
  height: 72px;
}

.gameQuestion {
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  text-align: left;
  font-size: 24px;
  line-height: 25px;
  color: #ffffff;
}

.cardGrid {
  background: #000000;
  border: 3px solid #ffffff;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.25);
  display: flex;
  flex-direction: column;
  height: 325px;
  padding: 20px 25px;
  width: 80%;
  /* justify-content: space-between; */
  text-align: left;
  justify-content: left;
}

.opening {
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 25px;
  color: rgba(255, 255, 255, 0.6);
  margin: 8px 0px;
}

.question {
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: min(4vw, 24px);
  height: 90px;
  text-align: left;
  color: #ffffff;
  margin: 8px 0px 20px;
}

.options {
  display: flex;
  justify-content: space-between;
  padding-top: 15px;
  width: 80%;
  margin: 0px auto;
}

.cardButton {
  border-radius: 50%;
  height: 75px;
  width: 75px;
  color: white;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 25px;
  text-align: center;
  color: #ffffff;
  border: none;
}

.yes {
  background: #11b854;
  box-shadow: 0px 0px 10px #11b854;
}

.no {
  background: #d02626;
  box-shadow: 0px 0px 10px #d02626;
}

.scoreContainer {
  display: block;
  flex-direction: column;
  width: 100%;
  /* background: red; */
  margin-top: 50px;
  height: 200px;
  z-index: 2;
  overflow: scroll;
  color: white;
  text-align: center;
  position: absolute;
  bottom: -25px;
}

.scoreGrid {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.scoreUserContainer {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* padding: 12px; */
  z-index: 3;
  height: 72px;
  /* position: fixed */
}

.userAvatar {
  border-radius: 50%;
  color: white;
  width: 40px;
  height: 40px;
  margin: 4px;
  border: none;
  /* flex: 1; */
}

.scoreUserName {
  text-align: left;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 25px;
  color: rgba(255, 255, 255, 0.9);
  margin: 4px;
}

.scoreUserStatus {
  /* flex: 3 */
  margin: 4px;
}

.scoreUserScore {
  flex: 1;
  text-align: right;
  margin-right: 12px;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 25px;
  color: #0047ff;
}

.lameContainer {
  /* padding-top: 200px; */
  /* position: relative;
    top: 30%; */
}
.lameTitle {
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 37px;
  color: #ffffff;
}

.gameContent {
  display: flex;
  flex-direction: column;
  height: 300px;
}

@media screen and (min-width: 1000px) {
  .question {
    font-family: Righteous;
    font-style: normal;
    font-weight: normal;
    font-size: min(2vw, 24px);
    height: 90px;
    color: #ffffff;
    margin: 8px 0px 32px;
  }
}
